import { notification } from 'ant-design-vue';
import GlobalIndicator from '@baifendian/adherev-ui-globalindicator';
import BfdUtil from '@baifendian/adherev-util';
import Intl from '@baifendian/adherev-util-intl';
import Ajax from './ajax';
var withVue = BfdUtil._util.withVue;
Ajax.isUse = function () { return true; };
Ajax.use = function (Vue) {
    GlobalIndicator.isUse() && GlobalIndicator.use(Vue);
    Intl.isUse() && Intl.use(Vue);
    Vue.use(notification);
    withVue(Vue, 'Ajax', Ajax);
};
export default Ajax;
